.download-btn .download-link {
    padding-left: 16px;
    font-size: 16px;
    font-weight: 400;
    margin-right: 10px
}

.download-popover {
    padding-left: 0;
    padding-right: 0
}

#el-popover-8008{
    width: 200px;
}

.select-platform {
    cursor: pointer;
    width: 40px;
    font-size: 0;
    text-align: center;
    outline: none
}

.select-platform > i {
    margin-right: 8px
}

.select-platform > i, .select-platform > span {
    display: inline-block;
    vertical-align: middle;
    height: 16px;
    line-height: 16px
}

.platform-list {
    list-style: none;
    font-size: 16px;
    padding: 0;
    margin: 0
}

.platform-list > li {
    cursor: pointer;
    padding: 10px 16px;
    font-size: 0
}

.platform-list > li > i {
    margin-right: 12px
}

.platform-list > li > i, .platform-list > li > span {
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    height: 16px;
    line-height: 16px
}

.platform-list > li:hover, .platform-list > li:hover > a {
    background-color: #5c5edc;
    color: #fff
}

.version {
    display: inline-block;
    font-size: 16px;
    color: #222;
    line-height: 40px
}

.version > sup {
    padding-left: 5px;
    font-weight: 700;
    color: #5c5edc
}

.footer {
    font-size: .75rem;
    line-height: 1rem;
    font-weight: 500;
    color: #595a5f;
    padding-bottom: 24px;
    text-align: center
}

.copyright {
    margin-right: 16px
}

.copyright, .locales {
    display: inline-block
}

.locales a.locale-active {
    color: #666
}

.locales em {
    color: #ccc;
    margin: 0 8px;
    font-style: normal
}

@media screen and (min-width: 1200px) {
    .footer {
        position: fixed;
        left: 48px;
        bottom: 18px;
        padding-bottom: 8px
    }
}
